import React, { Component } from "react";
import "./Skills.css";
import SoftwareSkill from "../../components/softwareSkills/SoftwareSkill";
import { directorMsg } from "../../portfolio";
import { Fade } from "react-reveal";
import DataScienceImg from "./DataScienceImg";
import FullStackImg from "./FullStackImg";
import CloudInfraImg from "./CloudInfraImg";
import DesignImg from "./DesignImg";

function GetSkillSvg(props) {
  console.log("props:", props.fileName, props);
  if (props.fileName === "engineers" || props.fileName === "doctors" || props.fileName === "director")
    return <DataScienceImg theme={props} />;
  else if (props.fileName === "CloudInfraImg")
    return <CloudInfraImg theme={props} />;
  return <DesignImg theme={props} />;
}

class DirectorMsgSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {directorMsg.data.map((skill) => {
          if (skill.show) {
            return (
              <div className="skills-main-div">
                <Fade left duration={2000}>
                  <div className="skills-image-div">
                    {/*img
                    alt="Ashutosh is Analysing Data"
                    src={require(`../../assests/images/${skill.filename}.svg`)}
                  ></img>*/}
                    <GetSkillSvg fileName={skill.fileName} theme={theme} />
                  </div>
                </Fade>

                <div className="skills-text-div">
                  <Fade right duration={1000}>
                    <h1
                      className="skills-heading"
                      style={{ color: theme.text }}
                    >
                      {skill.title}
                    </h1>
                  </Fade>
                  <Fade right duration={1500}>
                    <SoftwareSkill logos={skill.softwareSkills} />
                  </Fade>
                  <Fade right duration={2000}>
                    <div>
                      {skill.fileName === 'director' ?
                       <q>
                      {skill.skills.map((skillSentence) => {
                        return (
                          <p
                            className="subTitle skills-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {skillSentence}
                          </p>
                        );
                      })}
                     </q> 
                     : 
                     skill.skills.map((skillSentence) => {
                        return (
                          <p
                            className="subTitle skills-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {skillSentence}
                          </p>
                        );
                      })
                      }
                    </div>
                  </Fade>
                </div>
              </div>
            );
          }
          return null;
        })}
      </div>
    );
  }
}

export default DirectorMsgSection;
